body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    background: linear-gradient(90deg, #0062ff, #da61ff);
    height: 100vh;
    width: 100vw;
}
.loading {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100vw;
    background: linear-gradient(90deg, #0062ff, #da61ff);
}
.container {
    width: 80%;
    margin: 0 auto;
    overflow: hidden;
}

.header {
    color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
}

.header h1 {
    margin: 0;
    padding: 20px 0;
    font-size: 2.5em;
}

.header p {
    margin: 0;
    padding: 20px 0;
}

.button {
    background: #fff;
    color: #333;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin: 20px 0;
    text-decoration: none;
}

.button:hover {
    background: #333;
    color: #fff;
}

.userName_Container {
    display: flex;
    flex-direction: column;
    margin-top: 20px;
    width: 100%;
}

.swipeDown  {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 20px;
    color: #fff;
}

.swipeDown h2 {
    margin: 0;
    padding: 20px 0;
    font-size: 2em;
}

.options_container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    margin-top: 20px;
}

.options_container div {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 20px;
    border-radius: 5px;
    cursor: pointer;
    width: 45%;
}

.options_container div:hover {
    color: #fff;
}

.options_container div h2 {
    margin: 0;
    padding: 20px 0;
    font-size: 2em;
}

.option{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 20px;
    color: #fff;
}