.loading {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100vw;
    background: linear-gradient(90deg, #0062ff, #da61ff);
}
.container {
    width: 80%;
    margin: 0 auto;
    overflow: hidden;
}

.header {
    color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
}

.title {
    margin: 0;
    padding: 20px 0;
    font-size: 2.5em;
}

.testList {
    display: flex;
    flex-direction: column;
    margin-top: 20px;
    width: 100%;
}

.testHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #333;
    color: #fff;
    padding: 10px 20px;
    border-radius: 5px;
    margin: 10px 0;
    cursor: pointer;
    text-decoration: none;
    transition: background 0.3s, color 0.3s;
    width: 90%;
}

.test {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #fff;
    color: #000000;
    padding: 10px 20px;
    border-radius: 5px;
    margin: 10px 0;
    cursor: pointer;
    text-decoration: none;
    transition: background 0.3s, color 0.3s;
    width: 90%;
}

.test:hover {
    background: #000000;
    color: #fff;
}

.testDetails {
    font-size: 1.5em;
}

.testTable {
    display: flex;
    flex-direction: column;
    margin-top: 20px;
    width: 100%;
}

.testTable h2 {
    margin: 0;
    padding: 20px 0;
}

.testTable table {
    width: 100%;
    border-collapse: collapse;
}

.testTable th {
    color: #fff;
    padding: 10px 0;
}

.buttonContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    margin-top: 20px;
}

.button {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 20px;
    border-radius: 5px;
    background: #ff28f8;
    cursor: pointer;
    width: 100%;
}